.line1 {
  transform-origin: 0 0;
  transition: transform .4s ease-in-out;
}

.line2 {
  transition: transform .2s ease-in-out;
}

.line3 {
  transform-origin: 0 100%;
  transition: transform .4s ease-in-out;
}

input[type="checkbox"]:checked ~ .menu-list {
  transform: translateX(0);
}

